import { createSlice } from "@reduxjs/toolkit";

const loaderSlice = createSlice({
  name: "loader",
  initialState: {
    isLoading: true,
  },
  reducers: {
    finishLoading: (state) => {
      state.isLoading = false;
    },
  },
});

export const { finishLoading } = loaderSlice.actions;
export default loaderSlice.reducer;
